import { CloseOutlined, PlusSquareOutlined } from "@ant-design/icons";
import { Button, Form as AntForm, message } from "antd";
import { FastField, FieldArray, Formik } from "formik";
import React from "react";
import { useDispatch } from "react-redux";
import * as Yup from "yup";
import { createNewInvoiceIssues } from "../../actions/invoiceIssuesAction";
import { AntInput } from "../../customField/CreateAntField";

const FormInvoiceAdd = ({ typeModel, byStaff, setVisible }) => {
    const [form] = AntForm.useForm();
    const dispatch = useDispatch();

    const initialValues = {
        byStaff: byStaff,
        bySupplier: "",
        comoditys: [""],
        price: [""],
        quantity: [""],
    };

    const validationSchema = Yup.object().shape({
        bySupplier: Yup.string().required("Tn nh cung cp khng c b trng"),
        comoditys: Yup.lazy((val) =>
            Array.isArray(val)
                ? Yup.array().of(Yup.string().required("Comoditys is required."))
                : Yup.string().required("Comoditys is required.")
        ),
        price: Yup.lazy((val) =>
            Array.isArray(val)
                ? Yup.array().of(
                      Yup.number()
                          .required("Price is required.")
                          .min(1, "Must be greathan 1")
                          .integer()
                  )
                : Yup.number()
                      .required("Price is required.")
                      .min(1, "Must be greathan 1")
                      .integer()
        ),
        quantity: Yup.lazy((val) =>
            Array.isArray(val)
                ? Yup.array().of(
                      Yup.number()
                          .required("Quantity is required.")
                          .min(1, "Must be greathan 1")
                          .integer()
                  )
                : Yup.number()
                      .required("Quantity is required.")
                      .min(1, "Must be greathan 1")
                      .integer()
        ),
    });

    // TODO Thm ha n nhp

    const onSubmit = (values, formAction) => {
        dispatch(createNewInvoiceIssues(values));
        setVisible(false);
        message.success({
            content: `To ho n nhp thnh cng`,
            style: {
                position: "relative",
                top: 10,
                right: "-80vh",
            },
        });
        formAction.resetForm();
        formAction.setSubmitting(false);
    };

    return (
        <div className="form_invoice">
            <Formik
                initialValues={initialValues}
                validationSchema={validationSchema}
                onSubmit={onSubmit}
            >
                {(formikProps) => {
                    const { handleSubmit, isValid, isSubmitting } = formikProps;
                    return (
                        <AntForm form={form}>
                            <FastField
                                name="bySupplier"
                                label="Nh cung cp"
                                type="text"
                                placeholder="Nhp tn nh cung cp"
                                hasFeedback={true}
                                component={AntInput}
                            />
                            <FieldArray name="comoditys">
                                {(fieldArrayProps) => {
                                    const { push, remove, form } = fieldArrayProps;
                                    const { values } = form;
                                    const { comoditys } = values;
                                    return comoditys.map((item, index) => (
                                        <div key={index} className="input_comodity">
                                            <FastField
                                                name={`comoditys[${index}]`}
                                                label="Hng ho"
                                                type="text"
                                                placeholder="Hng ho"
                                                hasFeedback={true}
                                                component={AntInput}
                                            />
                                            <FastField
                                                name={`price[${index}]`}
                                                type="number"
                                                placeholder="n gi"
                                                hasFeedback={true}
                                                className="input_comodity__price"
                                                component={AntInput}
                                            />
                                            <FastField
                                                name={`quantity[${index}]`}
                                                type="number"
                                                placeholder="S lng"
                                                hasFeedback={true}
                                                component={AntInput}
                                                className="input_comodity__quantity"
                                            />
                                            {index > 0 && (
                                                <Button
                                                    className="input_comodity__btn_remove"
                                                    type="primary"
                                                    danger
                                                    onClick={() => remove(index)}
                                                    icon={<CloseOutlined />}
                                                />
                                            )}
                                            <Button
                                                type="primary"
                                                className="input_comodity__btn_add"
                                                onClick={() => push("")}
                                                icon={<PlusSquareOutlined />}
                                            />
                                        </div>
                                    ));
                                }}
                            </FieldArray>
                            <Button
                                onClick={handleSubmit}
                                disabled={!isValid || isSubmitting}
                                loading={isSubmitting}
                                type="primary"
                            >
                                {typeModel}
                            </Button>
                        </AntForm>
                    );
                }}
            </Formik>
        </div>
    );
};

export default FormInvoiceAdd;
