import { Button, Form as AntForm, message } from "antd";
import { FastField, Formik } from "formik";
import React from "react";
import { useDispatch } from "react-redux";
import * as Yup from "yup";
import { updateSupplier } from "../../actions/supplierAction";
import { AntInput } from "../../customField/CreateAntField";

const FormSupplierEdit = ({ supplier, setVisible, listSupplier }) => {
    const [form] = AntForm.useForm();
    const dispatch = useDispatch();
    const initialValues = supplier;
    const validationSchema = Yup.object().shape({
        name: Yup.string().required("Tn nh cung cp khng c b trng."),
        address: Yup.string().required("a ch khng c b trng."),
        phone: Yup.string()
            .required("S in thoi khng c b trng.")
            .min(9, "Ti thiu 9 s.")
            .max(9, "Ti a 9 s."),
    });

    // TODO Cp nht nh cung cp

    const onSubmit = (values, formAction) => {
        const newListSup = listSupplier.filter((item) => item._id !== supplier._id);
        const supExits = newListSup.find(
            (item) => item.name.toLowerCase() === values.name.toLowerCase()
        );
        if (supExits) {
            formAction.setSubmitting(false);
            return message.error({
                content: "Nh cung cp  tn ti",
                style: {
                    position: "relative",
                    top: 10,
                    right: "-80vh",
                },
            });
        }
        dispatch(updateSupplier(supplier._id, values));
        message.success({
            content: `Cp nht thnh cng`,
            style: {
                position: "relative",
                top: 10,
                right: "-80vh",
            },
        });
        setVisible(false);
        formAction.resetForm();
        formAction.setSubmitting(false);
    };

    return (
        <div className="form_invoice">
            <Formik
                initialValues={initialValues}
                validationSchema={validationSchema}
                onSubmit={onSubmit}
                enableReinitialize={true}
            >
                {(formikProps) => {
                    const { handleSubmit, isValid, isSubmitting } = formikProps;
                    return (
                        <AntForm form={form} encType="multipart/form-data">
                            <FastField
                                name="name"
                                label="Nh cung cp"
                                type="text"
                                placeholder="Nhp tn nh cung cp"
                                hasFeedback={true}
                                component={AntInput}
                            />
                            <FastField
                                name="address"
                                label="a ch"
                                type="text"
                                placeholder="Nhp a ch"
                                hasFeedback={true}
                                component={AntInput}
                            />
                            <FastField
                                name="phone"
                                label="ST (+84)"
                                type="text"
                                placeholder="Nhp tn nh cung cp"
                                hasFeedback={true}
                                component={AntInput}
                            />
                            <Button
                                onClick={handleSubmit}
                                disabled={!isValid || isSubmitting}
                                loading={isSubmitting}
                                type="primary"
                            >
                                Cp nht
                            </Button>
                        </AntForm>
                    );
                }}
            </Formik>
        </div>
    );
};

export default FormSupplierEdit;
