import { Button, Form as AntForm, message } from "antd";
import { FastField, Formik } from "formik";
import React from "react";
import { useDispatch } from "react-redux";
import * as Yup from "yup";
import { updateComodity } from "../../actions/comodityAction";
import { AntInput } from "../../customField/CreateAntField";

const FormComodityEdit = ({ comodity, setVisible, listComodity }) => {
    const [form] = AntForm.useForm();
    const dispatch = useDispatch();
    const initialValues = {
        ...comodity,
        ...comodity.unit,
    };
    const validationSchema = Yup.object().shape({
        name: Yup.string().required("Tn khng c b trng"),
        price: Yup.number()
            .required("n gi khng c b trng.")
            .integer("Phi l s")
            .min(1000, "Phi c gi tr hn 1000"),
        unit: Yup.number().required("n v tnh khng c b trng.").min(1),
        unitMath: Yup.string()
            .required("n v quy c khng c b trng.")
            .matches(
                /^[a-zA-Z_" +\\s]+$/,
                "Khng c c s."
            ),
    });

    // TODO Cp nht hng ha

    const onSubmit = (values, formAction) => {
        const newListCom = listComodity.filter((item) => item._id !== comodity._id);
        const comExits = newListCom.find(
            (item) => item.name.toLowerCase() === values.name.toLowerCase()
        );
        if (comExits) {
            formAction.setSubmitting(false);
            return message.error({
                content: `Hng ho  tn ti`,
                style: {
                    position: "relative",
                    top: 10,
                    right: "-80vh",
                },
            });
        }
        const unitId = comodity.unit && comodity.unit._id;
        dispatch(updateComodity({ ...values, unitId }, comodity._id));
        message.success({
            content: `Cp nht thnh cng`,
            style: {
                position: "relative",
                top: 10,
                right: "-80vh",
            },
        });
        setVisible(false);
        formAction.setSubmitting(false);
    };

    return (
        <div className="form_product">
            <Formik
                initialValues={initialValues}
                validationSchema={validationSchema}
                onSubmit={onSubmit}
                enableReinitialize={true}
            >
                {(formikProps) => {
                    const { handleSubmit, isValid, isSubmitting } = formikProps;
                    return (
                        <AntForm form={form} encType="multipart/form-data">
                            <FastField
                                name="name"
                                label="Tn hng ha"
                                type="text"
                                placeholder="Nhp tn hng ha"
                                hasFeedback={true}
                                component={AntInput}
                            />
                            <FastField
                                name="price"
                                label="n gi"
                                type="number"
                                placeholder="Nhp n gi"
                                hasFeedback={true}
                                component={AntInput}
                            />
                            <FastField
                                name="unit"
                                label="n v quy c"
                                type="number"
                                placeholder="Nhp n v quy c"
                                hasFeedback={true}
                                component={AntInput}
                            />
                            <FastField
                                name="unitMath"
                                label="n v tnh"
                                type="text"
                                placeholder="Nhp n v tnh"
                                hasFeedback={true}
                                component={AntInput}
                            />
                            <Button
                                onClick={handleSubmit}
                                disabled={!isValid || isSubmitting}
                                loading={isSubmitting}
                                type="primary"
                            >
                                Cp nht
                            </Button>
                        </AntForm>
                    );
                }}
            </Formik>
        </div>
    );
};

export default FormComodityEdit;
