import { Button, Form as AntForm, message } from "antd";
import { FastField, Formik } from "formik";
import React from "react";
import { useDispatch } from "react-redux";
import * as Yup from "yup";
import { addNewProduct } from "../../actions/productAction";
import { AntInput, AntSelect, UploadFile } from "../../customField/CreateAntField";

const FormProductAdd = ({
    listType,
    listComodity,
    setVisible,
    typeModel,
    listProduct,
}) => {
    const [form] = AntForm.useForm();
    const dispatch = useDispatch();
    const SUPPORTED_FORMATS = ["image/jpg", "image/jpeg", "image/gif", "image/png"];
    const NULL_FILE = null;
    const initialValues = {
        name: "",
        image: null,
        price: "",
        comoditys: [],
        type: listType[0]._id,
    };

    const validationSchema = Yup.object().shape({
        name: Yup.string().required("Tn mn khng c b trng"),
        price: Yup.number()
            .required("Gi khng c b trng")
            .min(1000, "Nh nht l 1000"),
        comoditys: Yup.array("")
            .required("Hng ha khng c b trng")
            .min(1, "Ti thiu t nht 1 hng ha"),
        image: Yup.mixed().test("fileFormat", "Khng ng nh dng file", (value) =>
            value === null ? NULL_FILE === null : SUPPORTED_FORMATS.includes(value.type)
        ),
    });

    // TODO Thm mn

    const onSubmit = (values, formAction) => {
        const productExist = listProduct.find(
            (item) => item.name.toLowerCase() === values.name.toLowerCase()
        );
        if (productExist) {
            formAction.setSubmitting(false);
            return message.error({
                content: `Sn phm  tn ti`,
                style: {
                    position: "relative",
                    top: 10,
                    right: "-80vh",
                },
            });
        }

        dispatch(addNewProduct(values));
        message.success({
            content: `Thm thnh cng`,
            style: {
                position: "relative",
                top: 10,
                right: "-80vh",
            },
        });
        setVisible(false);
        formAction.resetForm();
        formAction.setSubmitting(false);
    };

    return (
        <div className="form_product">
            <Formik
                initialValues={initialValues}
                validationSchema={validationSchema}
                onSubmit={onSubmit}
            >
                {(formikProps) => {
                    const { handleSubmit, isValid, isSubmitting } = formikProps;
                    return (
                        <AntForm form={form}>
                            <FastField
                                name="name"
                                label="Tn mn"
                                type="text"
                                placeholder="Nhp tn mn"
                                hasFeedback={true}
                                component={AntInput}
                            />
                            <FastField
                                name="image"
                                label="Hnh nh"
                                listType="picture"
                                hasFeedback={true}
                                component={UploadFile}
                            />
                            <FastField
                                name="price"
                                label="Gi"
                                type="number"
                                placeholder="Nhp gi"
                                hasFeedback={true}
                                component={AntInput}
                            />
                            <FastField
                                name="type"
                                label="Loi"
                                placeholder="Chn loi"
                                hasFeedback={true}
                                selectOptions={listType}
                                component={AntSelect}
                            />
                            <FastField
                                name="comoditys"
                                mode="multiple"
                                label="Thnh phn"
                                placeholder="Chn hng ho"
                                hasFeedback={true}
                                selectOptions={listComodity}
                                component={AntSelect}
                            />
                            <Button
                                onClick={handleSubmit}
                                disabled={!isValid || isSubmitting}
                                loading={isSubmitting}
                                type="primary"
                            >
                                {typeModel}
                            </Button>
                        </AntForm>
                    );
                }}
            </Formik>
        </div>
    );
};

export default FormProductAdd;
