import { Divider, Input } from "antd";
import React from "react";
import { formatMoney } from "../../utils/formatNumber";

const Payment = ({ pane, product, total, caculator, moneyPay, payment, setPayment }) => {
    return (
        <div className="payment_group">
            <p className="payment_money">
                Tng tin <span>{`${formatMoney(total(product))} VN`}</span>
            </p>
            <p className="payment_money">
                Khch cn tr
                <span className="payment_money_user">{`${formatMoney(
                    caculator()
                )} VN`}</span>
            </p>
            <div className="input_group">
                <label htmlFor="money">Tin khch a </label>
                <Input
                    id="money"
                    name="money"
                    type="number"
                    value={pane.payment ? pane.payment : payment}
                    onChange={(e) => setPayment(e.currentTarget.value)}
                />
                <span>(Mnh gi 1 = 1000 VN)</span>
            </div>
            <Divider />
            <p className="payment_money">
                Tin phi tr khch
                {pane.moneyPay && pane.moneyPay > 0 ? (
                    <span>{`${formatMoney(pane.moneyPay)} VN`}</span>
                ) : (
                    <span>
                        {moneyPay() < 0 ? "0 VN" : `${formatMoney(moneyPay())} VN`}
                    </span>
                )}
            </p>
        </div>
    );
};

export default Payment;
