import { Button, message, notification, Table } from "antd";
import Column from "antd/lib/table/Column";
import React from "react";

const TableSupplier = ({ listSupplier, showModal, removeSup }) => {
    return (
        <Table
            size={"small"}
            className="table_type"
            dataSource={listSupplier}
            rowKey="_id"
            pagination={{ defaultPageSize: 2, position: ["bottomCenter"] }}
        >
            <Column title="Nh cung cp" dataIndex="name" key="name" />
            <Column title="a ch" dataIndex="address" key="address" />
            <Column
                title="ST"
                dataIndex="phone"
                key="phone"
                render={(phone) => phone && <span>+84 {phone}</span>}
            />
            <Column
                colSpan="2"
                title="Action"
                dataIndex="_id"
                key="_id"
                render={(_id) => (
                    <div className="button_group">
                        <Button
                            type="primary"
                            danger
                            onClick={() => {
                                message.destroy();
                                return notification["error"]({
                                    key: `${_id}`,
                                    message: "Bn c ng  xo",
                                    btn: (
                                        <Button
                                            type="primary"
                                            size="small"
                                            onClick={() => {
                                                removeSup(_id);
                                                notification.close(_id);
                                            }}
                                        >
                                            Xc nhn
                                        </Button>
                                    ),
                                });
                            }}
                        >
                            Xo
                        </Button>
                        <Button
                            type="primary"
                            onClick={() => {
                                showModal("Cp nht nh cung cp", _id);
                            }}
                        >
                            Cp nht
                        </Button>
                    </div>
                )}
            />
        </Table>
    );
};

export default TableSupplier;
