import { Button, message, notification, Table } from "antd";
import Column from "antd/lib/table/Column";
import React from "react";

const TableType = ({ listType, showModal, deleteType }) => {
    return (
        <Table
            size={"small"}
            className="table_type"
            dataSource={listType}
            rowKey="_id"
            pagination={{ defaultPageSize: 3, position: ["bottomRight"] }}
        >
            <Column title="Tn loi" dataIndex="name" key="name" />
            <Column
                colSpan="2"
                title="Action"
                dataIndex="_id"
                key="_id"
                render={(_id) => (
                    <div className="button_group">
                        <Button
                            type="primary"
                            danger
                            onClick={() => {
                                message.destroy();
                                return notification["error"]({
                                    key: `${_id}`,
                                    message: "Bn c ng  xo loi ny",
                                    btn: (
                                        <Button
                                            type="primary"
                                            size="small"
                                            onClick={() => {
                                                deleteType(_id);
                                                notification.close(_id);
                                            }}
                                        >
                                            Xc nhn
                                        </Button>
                                    ),
                                });
                            }}
                        >
                            Xo
                        </Button>
                        <Button
                            type="primary"
                            onClick={() => showModal("Cp nht loi", _id)}
                        >
                            Cp nht
                        </Button>
                    </div>
                )}
            />
        </Table>
    );
};

export default TableType;
