import { Button, DatePicker, Divider, Form as AntForm, message } from "antd";
import { FastField, Formik } from "formik";
import React from "react";
import { useDispatch } from "react-redux";
import * as Yup from "yup";
import { createNewUser } from "../../actions/staffAction";
import { AntInput, AntSelect } from "../../customField/CreateAntField";
import moment from "moment";

const FormAddUser = ({ setVisible }) => {
    const [form] = AntForm.useForm();
    const dispatch = useDispatch();
    const optionSex = [
        { _id: true, name: "Nam" },
        { _id: false, name: "N" },
    ];
    const optionType = [
        { _id: 1, name: "Nhn vin phc v" },
        { _id: 2, name: "Nhn vin quy" },
        { _id: 3, name: "Nhn vin thu ngn" },
    ];
    const initialValues = {
        firstname: "",
        lastname: "",
        sex: "true",
        email: "",
        type: "1",
        password: "",
        birthday: moment(Date.now()),
    };
    const validationSchema = Yup.object().shape({
        firstname: Yup.string()
            .required("H khng c b trng.")
            .min(8, "Ti thiu 8 k t tr ln.")
            .max(18, "Ti a 18 k t.")
            .matches(
                /^[a-zA-Z_" +\\s]+$/,
                "Khng c c s."
            ),
        lastname: Yup.string()
            .required("Tn kkhng c b trng.")
            .min(2, "Ti thiu 2 k t tr ln.")
            .max(5, "Tida 5 k t.")
            .matches(
                /^[a-zA-Z_" +\\s]+$/,
                "Khng c c s."
            ),
        email: Yup.string()
            .required("Email khng c b trng.")
            .email("Khng ng nh dng email."),
        password: Yup.string().required("Password khng c b trng."),
    });

    // TODO Thm ngi dng

    const onSubmit = (values, formAction) => {
        dispatch(createNewUser(values))
            .then((data) => {
                if (data)
                    message.success({
                        content: `Thm thnh cng`,
                        style: {
                            position: "relative",
                            top: 10,
                            right: "-80vh",
                        },
                    });
            })
            .catch((err) => {
                message.error({
                    content: "Email  tn ti",
                    style: {
                        position: "relative",
                        top: 10,
                        right: "-80vh",
                    },
                });
            });

        setVisible(false);
        formAction.resetForm();
        formAction.setSubmitting(false);
    };

    return (
        <div className="form_invoice">
            <Formik
                initialValues={initialValues}
                validationSchema={validationSchema}
                onSubmit={onSubmit}
                enableReinitialize={true}
            >
                {(formikProps) => {
                    const { handleSubmit, isValid, isSubmitting } = formikProps;
                    return (
                        <AntForm form={form}>
                            <FastField
                                name="firstname"
                                label="H tn"
                                type="text"
                                placeholder="Eg: Nguyn Hu"
                                hasFeedback={true}
                                component={AntInput}
                            />
                            <FastField
                                name="lastname"
                                label="Tn"
                                type="text"
                                placeholder="Eg: Tn"
                                hasFeedback={true}
                                component={AntInput}
                            />
                            <FastField
                                name="sex"
                                label="Gii tnh"
                                placeholder="Eg: Nam"
                                hasFeedback={true}
                                selectOptions={optionSex}
                                component={AntSelect}
                            />
                            <label htmlFor="birthday">Ngy sinh: </label>
                            <FastField
                                name="birthday"
                                defaultValue={moment("1998-07-25")}
                                placeholder="Eg: 14/3/2020"
                                hasFeedback={true}
                                selectOptions={optionSex}
                                component={DatePicker}
                                className="input_date"
                            />
                            <Divider />
                            <FastField
                                name="email"
                                label="Email"
                                type="email"
                                placeholder="Eg: excample@gmail.com"
                                hasFeedback={true}
                                component={AntInput}
                            />
                            <FastField
                                name="password"
                                label="Password"
                                type="password"
                                hasFeedback={true}
                                component={AntInput}
                            />
                            <FastField
                                name="type"
                                label="Chc v"
                                hasFeedback={true}
                                component={AntSelect}
                                selectOptions={optionType}
                            />
                            <Button
                                onClick={handleSubmit}
                                disabled={!isValid || isSubmitting}
                                loading={isSubmitting}
                                type="primary"
                            >
                                Thm
                            </Button>
                        </AntForm>
                    );
                }}
            </Formik>
        </div>
    );
};

export default FormAddUser;
